import "core-js/modules/es.array.push.js";
import 'vant/es/toast/style';
import { createApp, ref } from 'vue';
import { Checkbox, CheckboxGroup, showFailToast, showSuccessToast, Toast } from "vant";
import axios from "axios";
import { store } from "@/store";
import { Image as VanImage } from 'vant';
const app = createApp();
app.use(Toast);
app.use(Checkbox);
app.use(CheckboxGroup);
app.use(VanImage);
export default {
  mounted() {
    this.init();
    console.log("是否登录：" + this.store.isLogin);
    console.log("用户信息：" + this.store.userInfo);
  },
  data() {
    return {
      checked: false,
      store,
      params: {}
    };
  },
  methods: {
    onClickLeft() {
      this.$router.push("/");
    },
    // 页面数据初始化
    init() {
      console.log("页面数据初始化");
      axios.get("/user/currentUser").then(res => {
        console.log("init:" + res);
        if (res.data.data != null) {
          this.store.userInfo = res.data.data;
          this.store.isLogin = true;
          sessionStorage.setItem("userInfo", JSON.stringify(res.data.data));
          console.log(sessionStorage.getItem("userInfo"));
          // 刷新后，重定向至“我的”
          this.$router.push("/my");
        }
      });
    },
    login() {
      let that = this;
      axios.post("/user/", this.params).then(res => {
        console.log(res);
        if (res.data.code === 6001) {
          this.store.userInfo = res.data.data;
          sessionStorage.setItem("userInfo", JSON.stringify(res.data.data));
          showSuccessToast(res.data.msg);
          console.log("session存：" + JSON.stringify(res.data.data));
          this.store.isLogin = true;
          // 两秒后跳转到首页
          setTimeout(function () {
            that.$router.push("/");
          }, 2000);
        } else {
          showFailToast(res.data.msg);
        }
      });
    },
    toLosePW() {
      this.$router.push({
        path: "/losepw",
        query: {
          userName: this.params.userName
        }
      });
    },
    register() {
      this.$router.push({
        path: "/register"
      });
    }
  }
};