import axios from "axios";
import { ref } from 'vue';
import { showToast } from 'vant';
export default {
  data() {
    return {
      h: '210px',
      subImages: [],
      goodInfo: {}
    };
  },
  setup() {
    const showShare = ref(false);
    const options = [{
      name: '微信',
      icon: 'wechat'
    }, {
      name: '支付宝',
      icon: 'alipay'
    }, {
      name: '复制链接',
      icon: 'link'
    }, {
      name: '分享海报',
      icon: 'poster'
    }, {
      name: '二维码',
      icon: 'qrcode'
    }];
    const onSelect = option => {
      showToast(option.name);
      showShare.value = false;
    };
    return {
      options,
      onSelect,
      showShare
    };
  },
  mounted() {
    let screenW = window.screen.width; //屏幕的宽
    let picW = 750; //图片的宽
    let picH = 750; //图片的高
    this.h = Math.floor(picH * screenW / picW) + 'px';
    console.log(this.$route.query.goodId);
    let goodId = this.$route.query.goodId;
    this.getGood(goodId);
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    getGood(goodId) {
      let that = this;
      axios.get("/good/" + goodId).then(res => {
        console.log(res);
        that.goodInfo = res.data.data;
        if (that.goodInfo.subImages != null) {
          that.subImages = res.data.data.subImages.split(",");
        }
        console.log(">>>>>" + that.subImages);
      });
    }
  },
  components: {}
};