import "core-js/modules/es.array.push.js";
import { store } from "@/store";
import axios from "axios";
import { showFailToast, showSuccessToast } from "vant";
export default {
  data() {
    return {
      store,
      isPass: false,
      question: "",
      answer: "",
      newPasswd: "",
      reNewPasswd: "",
      userInfo: {},
      userName: ""
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onSubmit() {
      axios.get("/user/forgetPasswd/" + this.userName).then(res => {
        if (res.data.code === 2001) {
          this.userInfo = res.data.data;
          this.question = res.data.data.question;
          console.log(">>>>>>>>>>>>>>>>" + this.question);
          this.isPass = true;
        } else {
          showFailToast(res.data.msg);
        }
      });

      // this.$router.push({path: "/losepw", query: {userName: this.userName}})
    },

    upPasswd() {
      let that = this;
      if (this.newPasswd !== this.reNewPasswd) {
        showFailToast("两次密码不一致");
        return;
      }
      let params = {
        userId: this.userInfo.userId,
        question: this.question,
        answer: this.answer,
        newPasswd: this.newPasswd,
        userName: this.userName
      };
      axios.put("/user/forgetPasswd", params).then(res => {
        if (res.data.code === 4001) {
          showSuccessToast(res.data.msg);
          setTimeout(function () {
            that.$router.push("/login");
          }, 2000);
        } else {
          showFailToast(res.data.msg);
        }
      });
    }
  },
  components: {},
  mounted() {
    this.userName = this.$route.query.userName;
  }
};